<template>
  <v-dialog
    :model-value="modelValue"
    max-width="400"
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-card :style="{ borderRadius: '24px' }">
      <v-card-title class="text-h6">
          
      </v-card-title>
      <v-card-text>
        <v-text-field
          v-model="title"
          label=" "
          variant="outlined"
          density="compact"
          autofocus
          :error="!!errorMessage"
          :error-messages="errorMessage"
          @keydown.enter="handleCreate"
        />
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          variant="text"
          @click="$emit('update:modelValue', false)"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          :loading="loading"
          :disabled="!title.trim()"
          @click="handleCreate"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, watch } from 'vue';
import { usePagesStore } from '../../stores/pages';

const props = defineProps({
  modelValue: {
    type: Boolean,
    required: true
  }
});

const emit = defineEmits(['update:modelValue', 'created']);

const pagesStore = usePagesStore();
const title = ref('');
const loading = ref(false);
const errorMessage = ref('');

watch(() => props.modelValue, (newVal) => {
  if (newVal) {
    title.value = '';
    errorMessage.value = '';
  }
});

const handleCreate = async () => {
  if (!title.value.trim()) return;
  
  loading.value = true;
  errorMessage.value = '';
  
  try {
    const page = await pagesStore.createPage(title.value.trim());
    emit('created', page);
    emit('update:modelValue', false);
    title.value = '';
    errorMessage.value = '';
  } catch (error) {
    console.error('   :', error);
    
    //     
    if (error.code === 'ERR_NETWORK' || error.message === 'Network Error') {
      errorMessage.value = ' .       CORS  .';
    } else if (error.response) {
      const status = error.response.status;
      const data = error.response.data;
      
      if (status === 401) {
        errorMessage.value = ' .    Telegram.';
      } else if (status === 403) {
        errorMessage.value = ' .  .';
      } else if (status === 400) {
        errorMessage.value = data?.message || ' .   .';
      } else {
        errorMessage.value = data?.message || `  (${status})`;
      }
    } else {
      errorMessage.value = error.message || ' ';
    }
  } finally {
    loading.value = false;
  }
};
</script>
