import axios from 'axios';
import { retrieveLaunchParams } from '@telegram-apps/sdk';

const api = axios.create({
  baseURL: process.env.VUE_APP_API_URL || 'https://owa.my-tg-notes.ru/test/api',
  timeout: 30000, // 30 
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json'
  }
});

api.interceptors.request.use((config) => {
  try {
    let initDataRaw = null;
    
    //    @telegram-apps/sdk
    try {
      const launchParams = retrieveLaunchParams();
      initDataRaw = launchParams?.initDataRaw;
      console.log('[API]  initDataRaw  SDK:', initDataRaw ? '' : '');
    } catch (e) {
      console.warn('[API] SDK ,  fallback:', e);
      // Fallback:   window.Telegram.WebApp
      if (window.Telegram?.WebApp?.initData) {
        initDataRaw = window.Telegram.WebApp.initData;
        console.log('[API]  initData  WebApp.initData');
      } else if (window.Telegram?.WebApp?.initDataUnsafe) {
        //  initData ,  initDataUnsafe ( )
        const initData = window.Telegram.WebApp.initDataUnsafe;
        //   initData 
        const params = new URLSearchParams();
        if (initData.user) {
          params.append('user', JSON.stringify(initData.user));
        }
        if (initData.auth_date) {
          params.append('auth_date', initData.auth_date);
        }
        if (initData.hash) {
          params.append('hash', initData.hash);
        }
        initDataRaw = params.toString();
        console.log('[API]  initDataRaw  initDataUnsafe');
      }
    }
    
    if (initDataRaw) {
      //     Authorization
      //      TelegramAuthFilter
      config.headers.Authorization = `tma ${initDataRaw}`;
      console.log('[API] :', config.method?.toUpperCase(), config.url);
      console.log('[API] Authorization  ');
    } else {
      console.warn('[API] initDataRaw  !     .');
    }
  } catch (e) {
    console.error('[API]    :', e);
  }
  return config;
});

//  
api.interceptors.response.use(
  (response) => {
    console.log('[API]  :', response.status, response.config.url);
    return response;
  },
  (error) => {
    //   
    if (error.response) {
      const { status, data } = error.response;
      console.error('[API]  :', {
        status,
        url: error.config?.url,
        method: error.config?.method,
        data
      });
      
      if (status === 401) {
        //  -   
        console.error('[API]  .    Telegram.');
      } else if (status === 403) {
        //   -    
        if (window.location.pathname !== '/subscription') {
          window.location.href = '/subscription';
        }
      } else if (status === 404) {
        //   
        console.error('[API]   ');
      }
    } else if (error.request) {
      //   ,    
      console.error('[API] Network Error -    :', {
        url: error.config?.url,
        method: error.config?.method,
        baseURL: error.config?.baseURL,
        message: error.message
      });
      
      //   
      if (error.code === 'ECONNABORTED') {
        console.error('[API]   ');
      } else if (error.code === 'ERR_NETWORK') {
        console.error('[API]  .  :');
        console.error('  - CORS  (      )');
        console.error('  -   SSL ');
        console.error('  -  ');
        console.error('  -  URL:', error.config?.baseURL + error.config?.url);
      }
    } else {
      //    
      console.error('[API]   :', error.message);
    }
    
    return Promise.reject(error);
  }
);

export default api;
